import * as React from 'react';

import { Button, Container, Divider, Pagination, Stack, Tab, TableFooter, TablePagination, Tabs } from '@mui/material';


import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell, { tableCellClasses } from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import { styled } from '@mui/styles';

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: '#E8EAED',
    color: '#202124',
    fontWeight: '600',
    padding: '10px 8px'
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 13,
    verticalAlign: 'middle',
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({

  // '&:nth-of-type(odd)': {
  //   backgroundColor: theme.palette.action.hover,
  // },
  // // hide last border
  // '&:last-child td, &:last-child th': {
  //   border: 0,
  // },
}));



function CustomTable(props) {

  return (
    <TableContainer sx={{ height: '90%' }}>
      <Table sx={{ minWidth: 600 }} stickyHeader aria-label="customized table">
        <TableHead>
          <TableRow>
            {props.headers?.map((head) => (
              <StyledTableCell>{head}</StyledTableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {props.transactions?.data.map((row) => (
            <StyledTableRow key={row.id}>
              <StyledTableCell  scope="row">
                {row.id}
              </StyledTableCell>
              <StyledTableCell scope="row">{row.customer}</StyledTableCell>
              <StyledTableCell scope="row" sx={{ color: row.action === 'debit' ? 'red' : 'green' }}>{row.action === 'debit' ? '-' : '+'} &#8377;{row.amount}</StyledTableCell>
              <StyledTableCell scope="row">{row.created_at}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
        <TableFooter>
          <TableRow>
            <TablePagination
              rowsPerPageOptions={[10]}
              // colSpan={3}

              count={props.transactions?.total || 0}
              rowsPerPage={10}
              page={props.page}

              onPageChange={props.handleChangePage}

            // onRowsPerPageChange={handleChangeRowsPerPage}
            // ActionsComponent={TablePaginationActions}
            />
          </TableRow>
        </TableFooter>
      </Table>
    </TableContainer>
  )
}

export default CustomTable;
