import { Fragment, useState } from 'react'
import {
  Avatar, Button, Divider, FormControl,
  Container, Typography
} from '@mui/material'

import { useAuthDispatch } from '../../context/auth'
import Link from '@mui/material/Link';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

import Box from '@mui/material/Box';


import ArrowRightAltIcon from '@mui/icons-material/ArrowRightAlt';
import { ValidationError } from 'yup';
import accountStyles from '../../utils/style/Account'
import { loginValidate } from '../../utils/validate/Account';

import toast from 'react-hot-toast'
import { CustomerAPI } from '../../apis/customer'
import { AxiosError } from 'axios'
import TxtField from '../Common/Field/TxtField'
import PasswordField from '../Common/Field/PasswordField'

import ReCAPTCHA from "react-google-recaptcha"
import React, { useRef } from 'react'


export default function Login(props) {
  const classes = accountStyles()
  const [values, setValues] = useState({
    email: '',
    password: ''
  })

  const captchaRef = useRef(null);
  const [errors, setErrors] = useState({})
  const dispatch = useAuthDispatch();

  const handleSubmit = async (event) => {
    event.preventDefault();
    try {
      // values.captcha = captchaRef.current.getValue();
      // captchaRef.current.reset();

      await loginValidate(values)
      console.log(values)
      const response = await CustomerAPI.get(values)
      dispatch({ type: 'LOGIN', payload: response })
    } catch (err) {
      if (err instanceof ValidationError) {
        var e = {}
        err.inner.forEach(({ path, message }) => {
          e[path] = message
          if (path === 'captcha') toast.error(message, { duration: 6000 });
        })
        setErrors(e)
      } else if (err instanceof AxiosError) {
        const error = err.response.data;
        toast.error(`${err.response.statusText} - ${error.message}`, { duration: 6000 });
      }
    }
  }

  const handleChange = (event) => {
    let { name, value } = event.target
    setValues({ ...values, [name]: value.trim() })
    setErrors(errors => ({ ...errors, [name]: null }))
  };

  return (
    <Fragment>

      <Container maxWidth="sm" sx={{ px: 1, py: 15, height: '98vh' }}>
        <Box >

        <div>
                <Typography component="h3" variant="h4" color="text.primary">
                  Sign in to Wallet
                </Typography>
                <Typography
                  variant="body1"
                  color="text.secondary"
                  sx={{ mb: { xs: 2, sm: 4 } }}
                >
                  Here you can provide a brief overview of the key features of the
                  product. For example, you could list the number of features, the types
                  of features, add-ons, or the benefits of the features.
                </Typography>
              </div>

          <form autoComplete='off' onSubmit={handleSubmit}>

            <FormControl sx={{ mt: 3 }} fullWidth >
              <TxtField
                handleChange={handleChange}
                label="email"
                name="email"
                autoFocus
                required
                error={errors.email}
              />
            </FormControl>

            <FormControl sx={{ my: 3 }} fullWidth >
              <PasswordField
                handleChange={handleChange}
                label='password'
                name='password'
                required
                error={errors.password}
              />
            </FormControl>
{/* 
            <FormControl sx={{ mb: 3, alignItems: 'center', width: '100%', p:0, m: 0 }} fullWidth >
              <ReCAPTCHA sitekey='6LcgdEwpAAAAANriCXwPDx2EDHhovBUTjjAsHXE4' ref={captchaRef} />
            </FormControl> */}

            <Button  onClick={() => props.changeAction('REGISTER')} >
              Create account
            </Button>
            <Button
              variant="contained"
              type="submit"
              disableElevation
              // className={classes.button}
              sx={{ float: 'right', background: "linear-gradient(#42a1ec,#0070c9)" }}
            >
              Sign In
            </Button>
          </form>

          <Container sx={{ mt: 4, textAlign: 'center' }}>
            <Divider sx={{ mb: 1.5 }} />
            <Link to="/password/verify" >
              Forgotten your password? <ArrowRightAltIcon sx={{ verticalAlign: 'middle'}}/>
            </Link>
          </Container>
        </Box>
      </Container>
    </Fragment>
  )
}
