import { api, objectToQueryString } from "./config"

export const TransactionAPI = {
    transfer: async function (payload) {
      const response = await api.request({
          url: `/transfers`,
          method: "POST",
          data: payload,
          headers: { 'Authorization': `Bearer ${sessionStorage.getItem('token')}`}
      })

      return response.data
    },

    history: async function (args) {
      const query = objectToQueryString(args)
      console.log(query)
      const response = await api.request({
          url: `/transfers/history${query}`,
          method: "GET",
          headers: { 'Authorization': `Bearer ${sessionStorage.getItem('token')}`}
      })
      return response.data
    },

    exportHistory: async function (args) {
      const query = objectToQueryString(args)
      console.log(query)
      const response = await api.request({
          url: `/export/transfers/history${query}`,
          method: "GET",
          // withCredentials: true,
          headers: { 'Authorization': `Bearer ${sessionStorage.getItem('token')}`}
      })
      return response.data
    }
}
