import * as React from 'react';

import { Button, Container, Divider, Tab, Tabs } from '@mui/material';
import CustomTable from '../Common/Table';

const WALLET_HEADERS = ['Txn ID', 'Customer', 'Amount', 'Timestamp']


export default function Transaction(props) {
  const [ action, setAction ] = React.useState('WALLET')
  const [ page, setPage ] = React.useState(0)

  const handleChangePage = (e, page) => {
    props.getTransactionHistory({ type: 'wallet', page: String(page + 1)})
    setPage(page)
  }

  return (
    <Container maxWidth="sm" sx={{ my: 2 }} >
      <Container>
        <Tabs
          value={action}
          variant="scrollable"
          aria-label="wrapped label tabs example"
        >
          <Tab
            value={action}
            label="Transfer Money"
            wrapped

            sx={{ textTransform: 'none' }}
          onClick={() => setAction('WALLET')}
          />
          <Tab value="RECHARGE" wrapped label="Recharge Card" sx={{ textTransform: 'none' }} onClick={() => setAction('RECHARGE')}/>
          <Tab value="REQUEST" wrapped label="Request Money" sx={{ textTransform: 'none' }} onClick={() => setAction('REQUEST')}/>
          {/* <Tab value="TRANSACTION" wrapped label="Show Transactions" sx={{textTransform: 'none'}}  /> */}
        </Tabs>
        <Divider />
      </Container>

      <CustomTable transactions={props.transactions} handleChangePage={handleChangePage} page={page} headers={WALLET_HEADERS}/>
    </Container>
  );
}
